// TODO
const IS_DEVTOOLS = false;
const BoxFit = {
  fill: 0,
  contain: 1,
  cover: 2,
  fitWidth: 3,
  fitHeight: 4,
  none: 5,
  scaleDown: 6
};
const image = {
  is: 'wx-image',
  options: {
    virtualHost: false
  },
  behaviors: ['wx-base', 'wx-cloud'],
  template: () => [],
  // template: function(m,f,e){return [{t:1,n:"image",id:"image",cl:{v:"image"},a:[],c:[]}]},
  properties: {
    src: {
      type: String,
      observer: 'srcChanged',
      public: true,
      value: 'scaleToFill'
    },
    mode: {
      type: String,
      observer: 'modeChanged',
      public: true,
      value: ''
    },
    // TODO
    lazyLoad: {
      type: Boolean,
      value: false,
      observer: 'lazyLoadChanged',
      public: true
    },
    showMenuByLongpress: {
      type: Boolean,
      value: false,
      public: true
    },
    // TODO
    lazyLoadMargin: {
      type: Number,
      value: 2,
      public: true,
      observer: 'lazyLoadMarginChanged'
    },
    // TODO
    referrerPolicy: {
      type: String,
      value: 'unsafe-url',
      public: true
    }
  },
  listeners: {
    longpress: '_onLongPress'
  },
  lifetimes: {
    created() {
      this._loaded = false;
      this._finalSrc = '';
      this._actualWidth = 0;
      this._actualHeight = 0;
      this._rect = {
        x: 0,
        y: 0,
        width: 0,
        height: 0
      };
    },

    attached() {
      this.$$.onLoadEvent((width, height) => {
        this.loaded = true;
        this._actualWidth = width;
        this._actualHeight = height;
        this.triggerEvent('load', {
          width,
          height
        });
      });
      this.$$.onErrorEvent(() => {
        this.triggerEvent('error', {
          errMsg: `load ${this._finalSrc} failed`
        });
      });
      this.$$.setLayoutCallback(rect => {
        this._rect = rect; // if (this.lazyLoad) {
        //   if (this.x + this.width > left && this.x < right && this.y + this.height > top && this.y < bottom) {
        //     this._showImage()
        //   }
        // }
      });
    }

  },
  methods: {
    _getImagePath(src) {
      if (typeof src === 'undefined' || src === '') return '';
      src = src.trim();

      if (/^\/\//.test(src)) {
        // #3550
        src = `http:${src}`;
      } // https://git.code.oa.com/wxweb/design/issues/4778 图片展示规则


      src = this.getRealRoute(src); // 转换成绝对路径

      return src;
    },

    _showImage() {
      this.getCloudFileUrl(this.src, 'image', src => {
        const _finalSrc = this._finalSrc = this._getImagePath(src) || src;

        this.loaded = false;
        this.$$.setAttributes({
          src: _finalSrc
        });
      });
    },

    srcChanged(newVal, oldVal) {
      if (newVal === oldVal) return;
      if (!this.lazyLoad) this._showImage();
    },

    modeChanged(newVal) {
      const validMode = ['widthFix', 'heightFix', 'scaleToFill', 'aspectFit', 'aspectFill', 'top', 'bottom', 'center', 'left', 'right', 'top left', 'top right', 'bottom left', 'bottom right'];

      if (validMode.indexOf(newVal) === -1) {
        return;
      }

      const modeMapToBoxFit = {
        'scaleToFill': BoxFit.fill,
        'aspectFit': BoxFit.contain,
        'aspectFill': BoxFit.cover,
        'widthFix': BoxFit.fitWidth,
        'heightFix': BoxFit.fitHeight,
        'top': BoxFit.none,
        'bottom': BoxFit.none,
        'center': BoxFit.none,
        'left': BoxFit.none,
        'right': BoxFit.none,
        'top left': BoxFit.none,
        'top right': BoxFit.none,
        'bottom left': BoxFit.none,
        'bottom right': BoxFit.none
      };
      this.$$.setAttributes({
        // @ts-ignore
        fit: modeMapToBoxFit[newVal]
      });
    },

    lazyLoadChanged(newVal, oldVal) {
      if (newVal === oldVal) return;
      if (!newVal) this._showImage();
    },

    _onLongPress() {
      if (!this.showMenuByLongpress || !this.loaded) {
        return;
      }

      if (IS_DEVTOOLS) {
        // @ts-ignore
        wx.showActionSheet({
          itemList: ['发送给朋友', '收藏', '保存图片', '识别图片的小程序码']
        });
        return;
      }

      let src = this.src;

      if (/^(cloud):\/\//.test(src)) {
        src = this._src;
      }

      src = src.trim();
      let imageBase64 = '';
      let imageUrl = '';

      if (/^data:image\//.test(src)) {
        imageBase64 = (src.split(',')[1] || '').trim();
      } else {
        // FIXME: window.__route__
        // @ts-ignore
        imageUrl = this.getRealRoute(src, window.__route__);
      } // @ts-ignore


      wx.invoke('showImageOperateSheet', {
        imageBase64,
        imageUrl,
        imageWidth: this._actualWidth,
        imageHeight: this._actualHeight,
        elementLeft: this._rect.x,
        elementTop: this._rect.y,
        elementWidth: this._rect.width,
        elementHeight: this._rect.height
      });
    }

  }
};
export { image };