import view from './wx-view';
import { text } from './wx-text';
import { image } from './wx-image';
import scrollView from './wx-scroll-view';
import map from './wx-map';
import video from './wx-video';
import './events';
import gestureHandlers from './wx-gesture';
import componentLess from './components.less';
export function registerBuiltinComponents(exparser, runtime) {
  // register stylesheets
  runtime.appendGlobalStylesheet(componentLess); // register components

  exparser.registerElement(view);
  exparser.registerElement(text);
  exparser.registerElement(image);
  exparser.registerElement(scrollView);
  exparser.registerElement(map);
  exparser.registerElement(video);
  gestureHandlers.forEach(handler => exparser.registerElement(handler));
}