// @ts-nocheck
const construstDetail = function (e) {
  return {
    propertyName: e.propertyName,
    elapsedTime: e.elapsedTime
  };
};

export function setupAnimationEventListeners(context, exparser) {
  ["transitionend"].forEach(evtName => {
    context.setAnimationEventListener(evtName, e => {
      console.log(`${evtName} trigger`);
      const options = {
        originalEvent: e,
        bubbles: true,
        capturePhase: true,
        composed: true,
        extraFields: {}
      };
      exparser.triggerEvent(e.target, evtName, construstDetail(e), options);
      document.dispatchEvent(new CustomEvent("pageReRender", {}));
    });
  });
}