const player = {
  is: 'wx-player',
  methods: {
    _formatTime(t, showHour = true) {
      // 直播流的时间是 Infinity
      if (t === Infinity) {
        return '00:00';
      }

      t = Math.floor(t);
      const h = Math.floor(t / 3600);
      let m = Math.floor((t - 3600 * h) / 60);
      const s = t - 3600 * h - 60 * m;

      if (h === 0 || !showHour) {
        m += h * 60;
        return `${m >= 10 ? m : '0' + m}:${s >= 10 ? s : '0' + s}`;
      }

      return `${h >= 10 ? h : '0' + h}:${m >= 10 ? m : '0' + m}:${s >= 10 ? s : '0' + s}`;
    }

  }
};
export default player;