const rpxReg = /[+-]?\d+(\.\d+)?rpx/;

const isRpxString = str => str.match(rpxReg);

const pxReg = /[+-]?\d+(\.\d+)?px/;

const isPxString = str => str.match(pxReg);

function transformSize(input) {
  // TODO: add transform RPX
  // if (isRpxString(input)) input = wx.transformRpx('%%?' + input + '?%%')
  if (isPxString(input)) input = parseFloat(input);
  input = +input;
  if (typeof input === "number" && !isNaN(input)) return input;
  return 0;
}

function normalizeSize(value) {
  if (typeof value === "string") return transformSize(value);
  if (typeof value === "number") return value;
  return 0;
}

export { normalizeSize };