import { LogLevel } from './log_level';

/**
 * Global environment configurations
 */
export const IS_DEV = process.env.ENV == 'dev';
export const IS_PROD = process.env.ENV == 'prod';
/**
 * silence = 0,
 * trace = 1,
 * info = 2,
 * warning = 3,
 * error = 4,
 * fatal = 5,
 */

const MINIMUM_LOG_LEVEL = parseInt(process.env.LOG_LEVEL);
export function getLogMinimumLogLevel() {
  if (MINIMUM_LOG_LEVEL == 0) {
    return LogLevel.silence;
  }

  if (MINIMUM_LOG_LEVEL == 1) {
    return LogLevel.trace;
  }

  if (MINIMUM_LOG_LEVEL == 2) {
    return LogLevel.info;
  }

  if (MINIMUM_LOG_LEVEL == 3) {
    return LogLevel.warning;
  }

  if (MINIMUM_LOG_LEVEL == 4) {
    return LogLevel.error;
  }

  if (MINIMUM_LOG_LEVEL == 5) {
    return LogLevel.fatal;
  }

  return LogLevel.info;
}