// @ts-nocheck
import RuntimeCore from '../../runtime';
const recognizerTypes = ['tap', 'doubleTap', 'longPress', 'verticalDrag', 'horizontalDrag', 'pan', 'scale', 'forcePress'];
const gestureHandlers = recognizerTypes.map(type => {
  const option = {
    is: `wx-${type}-gesture-handler`,
    template: function (m, f, e) {
      return [{
        t: 1,
        n: "gesture",
        id: "gesture",
        a: [],
        c: [{
          t: 1,
          n: "slot",
          v: true,
          sn: "",
          a: [],
          c: []
        }]
      }];
    },
    options: {
      multipleSlots: true
    },
    properties: {
      onGestureEvent: {
        type: String,
        public: true
      }
    },
    lifetimes: {
      attached() {
        const module = RuntimeCore.getNativeGlobal().ReanimatedModule;
        const runtime = RuntimeCore.getInstance();
        const exparser = runtime.getExparser();
        const funcName = this.data.onGestureEvent;
        const host = this.ownerShadowRoot.getHostNode();
        const caller = exparser.Element.getMethodCaller(host);
        const onGestureEventFn = caller[funcName];
        const onGestureEventWorkletFactory = caller[`_${funcName}_worklet_factory_`];
        this.$.gesture.$$.setAttributes({
          recognizerType: type
        });

        if (onGestureEventWorkletFactory) {
          console.log(`registerEventHandler ${funcName}`);
          const onGestureEventWorkletFn = onGestureEventWorkletFactory.call(host);
          const eventHash = this.$.gesture.$$.viewTag + type;
          module.registerEventHandler(eventHash, onGestureEventWorkletFn);
        } else if (onGestureEventFn) {
          this.$.gesture.$$.subscribe(type, gestureEvent => {
            onGestureEventFn.call(host, gestureEvent);
          });
        }
      }

    }
  };
  return option;
});
export default gestureHandlers;