// AddEvent callback
const EventMap = new Map();

function triggerEvent(name, res) {
  if (EventMap.has(name)) {
    // @ts-ignore
    EventMap.get(name)(res);
  }
}

if (process.env.isWechat) {
  wx.on('onXWebVideoPreloadedMetaData', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoPreloadedMetaData', {
      viewId,
      preloadSize: res.preloadSize
    });
  });
  wx.on('onXWebVideoPlay', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoPlay', {
      timeStamp: res.timeStamp,
      viewId,
      type: res.type
    });
  });
  wx.on('onXWebVideoPause', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoPause', {
      viewId,
      type: res.type
    });
  });
  wx.on('onXWebVideoLoadedMetaData', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoLoadedMetaData', {
      viewId,
      duration: res.duration,
      width: res.width,
      height: res.height,
      type: res.type
    });
  });
  wx.on('onXWebVideoWaiting', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoWaiting', {
      viewId,
      type: res.type
    });
  });
  wx.on('onXWebVideoTimeUpdate', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoTimeUpdate', {
      viewId,
      position: res.position,
      duration: res.duration,
      type: res.type
    });
  });
  wx.on('onXWebVideoProgress', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoProgress', {
      viewId,
      buffered: res.buffered,
      type: res.type
    });
  });
  wx.on('onXWebVideoEnded', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoEnded', {
      viewId,
      type: res.type
    });
  });
  wx.on('onXWebCastingUserSelect', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoCastingUserSelect', {
      viewId,
      state: typeof res.state !== 'undefined' ? res.state : res.status
    });
  });
  wx.on('onXWebDLNACastingStateChange', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoCastingStateChange', {
      viewId,
      state: res.state || res.status
    });
  });
  wx.on('onXWebCastingInterrupt', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoCastingInterrupt', {
      viewId
    });
  });
  wx.on('onXWebVideoOrientationChanged', function (res) {
    triggerEvent('onVideoOrientationChanged', res);
  });
  wx.on('onXWebVideoBackgroundPlaybackChange', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoBackgroundPlaybackChange', {
      viewId,
      playInBackground: res.playInBackground
    });
  });
  wx.on('onXWebVideoError', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoError', {
      viewId,
      errMsg: res.errMsg
    });
  });
  wx.on('onXWebVideoSeekComplete', function (res) {
    const {
      viewId
    } = JSON.parse(res.data);
    triggerEvent('onVideoSeekCompelete', {
      viewId,
      position: res.position
    });
  });
}

