// FIXME: can not import stopMapper
const base = {
  is: 'wx-base',
  properties: {
    hidden: {
      type: Boolean,
      public: true
    },
    animation: {
      type: Object,
      observer: 'animationChanged'
    }
  },
  data: {
    mapperIds: []
  },

  detached() {
    const self = this;
    self.stopMappers();
  },

  methods: {
    publish(method, args) {
      const pageId = this.getPageId();
      wx.subContext.publish(method, args, pageId);
    },

    invoke(method, args, callback) {
      args.__invoke_as_webview = true;
      args.__invoke_webviewId = this.getPageId();
      wx.invoke(method, args, res => {
        if (typeof callback === 'function') {
          callback.call(this, res);
        }
      });
    },

    getNodeId() {
      this.__nodeId = this.__nodeId || (this.ownerShadowRoot ? __virtualDOM__.getNodeId(this.ownerShadowRoot.__wxHost) : '');
      return this.__nodeId;
    },

    getPageId() {
      this.__pageId = this.__pageId || __virtualDOM__.getPageId(this) || __virtualDOM__.getPageId(this.ownerShadowRoot.__wxHost);
      return this.__pageId;
    },

    getRealRoute(src, currentRoute = '') {
      // 相对路径
      if (!(/^wxfile:\/\//.test(src) || /^(http|https):\/\//.test(src) || /^\s*data:image\//.test(src))) {
        if (this.ownerShadowRoot) {
          const domain = this.ownerShadowRoot.__wxHost.__componentOptions.domain;

          if (domain && domain !== '/' && domain.indexOf('simple://') === -1) {
            // for plugin
            const route = this.ownerShadowRoot.__wxHost.is.replace(/^plugin-private:\/\/wx[^/]+\//, '');

            const pluginId = domain.split('/')[0]; // @ts-ignore

            src = __virtualDOM__.getPluginRoutePrefix(pluginId) + wx.getRealRoute(route, src);
          } else {// 引用分包下的扩展库组件时，拼上分包路径前缀，客户端才能够正确读取到
            // 开发者工具会把扩展库的所有组件列表打进配置，命中扩展库组件时补上分包路径
            // const currentPkg = (window.__subPackages || __wxConfig.subPackages || [])
            //   .filter(pkg => (window.__route__ || '').indexOf(pkg.root) === 0)
            // const currentPkgName = currentPkg[0] ? currentPkg[0].root || '' : ''
            // const allExtendedComponents = currentPkg[0] ? currentPkg[0].allExtendedComponents || [] : []
            // let compPath = this.ownerShadowRoot.__wxHost.is
            // compPath = allExtendedComponents.indexOf(compPath) > -1 ? currentPkgName + compPath : compPath
            // src = wx.getRealRoute(compPath, src)
          } // if (IS_DEVTOOLS) {
          //   src = '/' + src
          // }

        } // else if (!IS_DEVTOOLS && currentRoute) {
        //   src = wx.getRealRoute(currentRoute, src)
        // }

      }

      return src;
    },

    animationChanged(newVal) {
      const self = this;
      self.stopMappers();
      const animationStyles = [];

      if (Array.isArray(newVal)) {
        animationStyles.push(...newVal);
      } else {
        animationStyles.push(newVal);
      }

      animationStyles.forEach(style => {
        const mapperId = style.mapperId;
        self.data.mapperIds.push(mapperId);
        self.$$.addAnimatedStyle(style);
      });
    },

    stopMappers() {
      const self = this;
      const mapperIds = self.data.mapperIds;
      mapperIds.forEach(mapperId => {
        self.$$.stopMapper(mapperId);
      });
    }

  }
};
export default base;