// app-service 过来的事件
// ref: wx-component/src/events/appservice.js
import RuntimeCore from '../../runtime';
import { subscribe } from './bridge';

const triggerEvent = (id, evName, detail, options) => {
  const root = __virtualDOM__.getRootNode(id);

  console.log(`id=${id}, evName=${evName}, detail=${JSON.stringify(detail)}, options=${JSON.stringify(options)}`);
  console.log(root);
  RuntimeCore.getInstance().getExparser().triggerEvent(root, evName, detail, options);
};

subscribe('operateMap', res => {
  triggerEvent(res.pageId, 'operateMap', res, {
    bubbles: true
  });
});