export const createEmitter = () => {
  let emitted = false;
  let res;
  const callbacks = [];
  return {
    emit(_res) {
      if (emitted) return;
      emitted = true;
      res = _res;
      callbacks.forEach(cb => cb(res));
    },

    on(cb) {
      if (emitted) cb(res);else callbacks.push(cb);
    }

  };
};