const assignedPositioningId = {};

const generatePositioningId = () => {
  for (;;) {
    // https://git.code.oa.com/wxweb/design/issues/4530
    const newId = Math.floor(Math.random() * 0x7fff) + 10;

    if (!assignedPositioningId[newId]) {
      assignedPositioningId[newId] = true;
      return newId;
    }
  }
};

const positioning = {
  is: "wx-positioning-target",

  created() {
    this._positioningId = generatePositioningId(); // this._parentPositioningContainer = null
    // this._positioningSyncing = false
  },

  methods: {
    getPositioningId() {
      return this._positioningId;
    },

    regeneratePositioningId() {
      this._positioningId = generatePositioningId();
      return this._positioningId;
    }

  }
};
export default positioning;