import { getLogMinimumLogLevel } from './env';
import { LogLevel } from './log_level';
const minimumLogLevel = getLogMinimumLogLevel();

class Logger {
  // log to trace method calls
  // recommand leaving trace call for later investigation and debug
  static trace(...args) {
    if (minimumLogLevel > LogLevel.trace) return;
    console.log(args.join(' '));
  } // log for local debug


  static info(...args) {
    if (minimumLogLevel > LogLevel.info) return;
    console.log(args.join(' '));
  } // log for warning


  static warn(...args) {
    if (minimumLogLevel > LogLevel.warning) return; // FIXME: support console.warn

    console.log(args.join(' '));
  } // log for error


  static error(...args) {
    if (minimumLogLevel > LogLevel.error) return;
    console.log(args.join(' '));
  } // log for unrecoverable fatal error
  // probably will be reported to server


  static fatal(...args) {
    if (minimumLogLevel > LogLevel.fatal) return;
    console.log(args.join(' '));
  }

}

export { Logger };