function handleTouchEvent(exparser, e) {
  const extraFields = {
    _requireActive: false,
    _relatedInfo: undefined,
    _allowWriteOnly: true,
    touches: e.touches || [],
    changedTouches: e.changedTouches || []
  };

  try {
    const exparserEvent = exparser.Event.create(e.type, {}, {
      originalEvent: e,
      bubbles: true,
      capturePhase: true,
      composed: true,
      extraFields
    });
    const traceId = typeof wx !== 'undefined' ? wx.traceBeginEvent('Exparser', 'dispatchEvent') : 0;
    exparser.Event.dispatchEvent(e.target, exparserEvent);
    if (typeof wx !== 'undefined') wx.traceEndEvent(traceId);
  } catch (error) {
    console.log('dispatchEvent error', error.message);
  }
}

const touchEvents = ['touchstart', 'touchmove', 'touchend', 'touchcancel', 'tap', 'longpress'];
export function setupTouchEventListeners(context, exparser) {
  touchEvents.forEach(evtName => {
    console.log(`register ${evtName}`);
    context.setTouchEventListener(evtName, evt => {
      if (typeof wx !== 'undefined') wx.traceFlag('SkylineEvent', evtName); // console.log(`on ${evtName}`, evt)

      handleTouchEvent(exparser, evt); // NOTE: also trigger longtap for compatibility reason

      if (evt.type == 'longpress') {
        const longTapEvent = Object.assign({}, evt);
        longTapEvent.type = "longtap";
        handleTouchEvent(exparser, longTapEvent);
      }
    });
  });
}