// @ts-nocheck
const INIT_STATUS = {
  NONE: 0,
  INITIALIZING: 1,
  DONE: 2
};
let initStatus = INIT_STATUS.NONE;
let reportQueue = [];
let _networkType = 'unknown';

const clearQueue = () => {
  for (let i = 0, imax = reportQueue.length; i < imax; i++) {
    const fn = reportQueue[i];

    try {
      fn();
    } catch (err) {
      wxConsole.log('[cloud] clearQueue error: ', err);
    }
  }

  reportQueue = [];
};

export const init = () => {
  if (initStatus !== INIT_STATUS.NONE) {
    return;
  }

  initStatus = INIT_STATUS.INITIALIZING;

  try {
    wx.getNetworkType({
      success: res => {
        _networkType = res.networkType;
      },
      complete: () => {
        initStatus = INIT_STATUS.DONE;
        clearQueue();
      }
    });
    wx.subscribe('onNetworkStatusChange', res => {
      if (initStatus !== INIT_STATUS.DONE) {
        initStatus = INIT_STATUS.DONE;
        clearQueue();
      }

      _networkType = res.networkType;
    });
  } catch (err) {
    wxConsole.log('[cloud] report init failed: ', err);

    if (initStatus !== INIT_STATUS.DONE) {
      initStatus = INIT_STATUS.DONE;
    }
  }
}; // 云 api 耗时上报 https://mmdata.oa.com/dist/#/protocol_detail?log_id=16588&_k=sz637q

function reportTimeCost(api, totalTime, totalTimeNoCSNetCost) {
  wxConsole.log('reportTimeCost', api, totalTime, totalTimeNoCSNetCost);

  if (initStatus !== INIT_STATUS.DONE) {
    if (initStatus !== INIT_STATUS.INITIALIZING) {
      init();
    }

    if (reportQueue.length < 50) {
      reportQueue.push(reportTimeCost.bind(this, api, totalTime, totalTimeNoCSNetCost));
      return;
    } else {
      // potential problem
      clearQueue();
    }
  }

  const value = [0, 'comp.cloud.getTempFileURL', totalTime, totalTime, 0, _networkType, totalTimeNoCSNetCost, totalTimeNoCSNetCost, 0].join(',');
  wxConsole.log('[reportTimeCost] Reporter.reportKeyValue', value);
  Reporter.reportKeyValue({
    key: 'CloudReport',
    value,
    force: true
  });
}

const wait = 50; // 50ms执行一次

const CB_INFO = {
  image: [],
  // item形如{src: xxx, callback: funciton}
  audio: [],
  video: []
}; // 直接复用了underscore的throttle函数

function throttle(func, wait, options) {
  let context;
  let args;
  let result;
  let timeout = null; // 上次执行时间点

  let previous = 0;
  if (!options) options = {}; // 延迟执行函数

  const later = function () {
    // 若设定了开始边界不执行选项，上次执行时间始终为0
    previous = options.leading === false ? 0 : new Date().getTime();
    timeout = null;
    result = func.apply(context, args);
    if (!timeout) context = args = null;
  };

  return function () {
    const now = new Date().getTime(); // 首次执行时，如果设定了开始边界不执行选项，将上次执行时间设定为当前时间。

    if (!previous && options.leading === false) previous = now; // 延迟执行时间间隔

    const remaining = wait - (now - previous);
    context = this; // eslint-disable-next-line

    args = arguments; // 延迟时间间隔remaining小于等于0，表示上次执行至此所间隔时间已经超过一个时间窗口
    // remaining大于时间窗口wait，表示客户端系统时间被调整过

    if (remaining <= 0 || remaining > wait) {
      clearTimeout(timeout);
      timeout = null;
      previous = now;
      result = func.apply(context, args);
      if (!timeout) context = args = null; // 如果延迟执行不存在，且没有设定结尾边界不执行选项
    } else if (!timeout && options.trailing !== false) {
      timeout = setTimeout(later, remaining);
    }

    return result;
  };
}

function getTempUrl(type) {
  // 清空以前的记录
  const cbInfo = CB_INFO[type].slice();
  CB_INFO[type] = [];
  const fileIds = [];
  cbInfo.forEach(item => {
    fileIds.push(item.src);
  });
  const startTime = Date.now();
  wx.getCloudFileUrl({
    data: {
      fileid_list: fileIds,
      type
    },

    success(res) {
      wxConsole.log('getCloudFileUrl', res);
      const list = res.data;
      const map = new Map();
      list.forEach((item, ind) => {
        if (item.code) {
          wxConsole.error(`getCloudFileUrl error fileId "${fileIds[ind]}": ${item.code} ${item.msg}`);
        }

        map.set(fileIds[ind], item);
      });
      cbInfo.forEach(item => {
        const tempInfo = map.get(item.src);
        const tempUrl = tempInfo.url;
        item.callback && item.callback(tempUrl || item.src); // sessionStorage进行缓存
        // tempInfo.create_time = new Date().getTime();
        // let storeSrc = 'servicewechat_' + window.__appId__ + '_' + item.src;
        // if (tempUrl) sessionStorage.setItem(storeSrc, JSON.stringify(tempInfo));
      });

      if (res.cost) {
        reportTimeCost('component.cloud.getTempFileURL', Date.now() - startTime, res.cost);
      }
    },

    fail() {
      cbInfo.forEach(item => {
        item.callback && item.callback(item.src);
      });
    } // complete(res) {
    //   wxConsole.log('getCloudFileUrl callback', res)
    // }


  });
}

const getImageUrl = throttle(getTempUrl, wait);
const getAudioUrl = throttle(getTempUrl, wait);
const getVideoUrl = throttle(getTempUrl, wait);
const PROCEDURE = {
  image: getImageUrl,
  audio: getAudioUrl,
  video: getVideoUrl
};
const cloud = {
  is: 'wx-cloud',
  methods: {
    getCloudFileUrl(src, type, callback) {
      // 非云文件ID直接返回
      if (!/^(cloud):\/\//.test(src)) return callback(src); // 缓存期内直接使用旧地址
      // let storeSrc = 'servicewechat_' + window.__appId__ + '_' + src;
      // let local = JSON.parse(sessionStorage.getItem(storeSrc) || '""');
      // let isValid = local && (local.create_time + local.max_age*1000 > new Date().getTime());
      // if (isValid) return callback(local.download_url);
      // 批量处理

      CB_INFO[type].push({
        src,
        callback
      });
      PROCEDURE[type](type);
    },

    // 批量获取临时文件地址，一次最大50条
    getCloudFileUrls(fileIds = [], type, callback) {
      if (fileIds.length === 0) {
        typeof callback === 'function' && callback(fileIds);
        return;
      }

      wx.getCloudFileUrl({
        data: {
          fileid_list: fileIds,
          type
        },

        success(res) {
          const list = res.data;
          callback(list.map((info, ind) => {
            if (info.code) {
              wxConsole.error(`getCloudFileUrl error fileId "${fileIds[ind]}": ${info.code} ${info.msg}`);
            }

            return info.url;
          }));
        },

        fail() {
          typeof callback === 'function' && callback(fileIds);
        },

        complete(res) {
          wxConsole.log('getCloudFileUrls callback', res);
        }

      });
    }

  }
};
export default cloud;