const filed = {
  is: 'wx://form-field',
  properties: {
    name: {
      type: String,
      public: true
    },
    value: {
      type: null,
      public: true
    }
  }
};
const styleFilter = {
  is: 'wx://style-filter',
  methods: {
    _styleFilter() {
      // TODO 目前是拦截掉所有style设置
      return '';
    }

  }
};
const requestFileter = {
  is: 'wx://request-filter',
  relations: {
    'wx://request-sender': {
      type: 'shadowContent'
    }
  },
  methods: {
    _srcFilter(src) {
      if (src.indexOf('://') !== -1 || src.indexOf('//') === 0) {
        // 网络请求
        if (src.indexOf('https://wx.qlogo.cn') === 0) {
          // 头像，直接放过
          return src;
        }

        return null;
      }

      return src;
    }

  }
};
const requestSender = {
  is: 'wx://request-sender',
  relations: {
    'wx://request-filter': {
      type: 'shadowHost',

      linked(target) {
        // @ts-ignore
        this._hasCheckedLinked = true; // @ts-ignore

        this._filterTarget = target; // @ts-ignore

        this._checkDeferredFuncList();
      },

      linkFailed() {
        // @ts-ignore
        this._hasCheckedLinked = true; // @ts-ignore

        this._checkDeferredFuncList();
      }

    }
  },
  methods: {
    /**
     * 检查属性是否被动态修改，是的话走过滤器
     */
    checkProp(value, propName = 'src') {
      value = value === undefined ? this[propName] : value;

      if (this._filterTarget) {
        // linked成功，需要走检查过滤
        return this._filterTarget._srcFilter(value);
      }

      return value;
    },

    /**
     * 保证某函数可以延迟到linked/linkFailed后执行
     */
    deferredSetProp(func) {
      // 已执行了linked/linkFailed，则立即执行
      if (this._hasCheckedLinked) return func && func(); // 否则延迟到linked/linkFailed之后执行

      this._deferredFuncList = this._deferredFuncList || [];

      this._deferredFuncList.push(func);
    },

    /**
     * 检测并执行延迟函数
     */
    _checkDeferredFuncList() {
      let func;
      const deferredFuncList = this._deferredFuncList;
      if (!deferredFuncList || !deferredFuncList.length) return; // eslint-disable-next-line

      while (func = deferredFuncList.shift()) {
        func();
      }
    }

  }
};
export { requestFileter, requestSender };